<template>
  <div id="container" >
    <div id="left" >
      <el-calendar v-model="value" :style="{ height: '100%' }"></el-calendar>
    </div>
    <div id="right" >
      <el-form ref="form" :rules="rules" :model="form" label-width="100px">
        <el-form-item label="底薪" prop="basic_salary">
          <el-input v-model="form.basic_salary" placeholder="单位为元" style="width: 200px"></el-input>
        </el-form-item>

        <el-form-item label="应出勤天数" prop="attendance_day">
          <el-input v-model="form.attendance_day" placeholder="单位为日" style="width: 200px"></el-input>
        </el-form-item>

        <el-form-item label="社保" prop="social_security">
          <el-input v-model="form.social_security" placeholder="单位为元" style="width: 200px"></el-input>
        </el-form-item>

        <el-form-item label="是否全勤">
          <el-select v-model="form.select" placeholder="请选择是否全勤" style="width: 200px">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="请假时间" v-if="form.select==0" prop="leave_time">
          <el-input v-model="form.leave_time" placeholder="请假时长，单位为小时" style="width: 200px"></el-input>
        </el-form-item>

        <el-form-item label="加班时间" >
          <el-input v-model="form.work_overtime" placeholder="加班时长，单位为小时" style="width: 200px"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onStart()">计算</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
      
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        basic_salary: '',
        select: '1',
        leave_time: '',
        work_overtime: '',
        social_security: '',
        attendance_day: '',
      },
      value: new Date(),
      rules: {
        basic_salary: [
          { required: true, message: '请输入正确的格式', trigger: 'blur', type: 'number', validator: this.validateNumber }
        ],
        leave_time: [
          { required: false, message: '请输入正确的格式', trigger: 'blur', type: 'number', validator: this.validateNumber }
        ],
        social_security: [
          { required: true, message: '请输入正确的格式', trigger: 'blur', type: 'number', validator: this.validateNumber }
        ],
        attendance_day: [
          { required: true, message: '请输入正确的格式', trigger: 'blur', type: 'number', validator: this.validateNumber }
        ]
      },
      perfect_attendance: 100,
      work_overtime_salary: 40,
      result: 0
    };
  },
  methods: {
    validateNumber(rule, value, callback) {
      if (/^\d+(\.\d+)?$/.test(value)) {
        callback();
      } else {
        callback(new Error('请输入正确的格式'));
      }
    },
    async onStart(formName) {
  if (this.form.select == 1) {
    this.result=Math.trunc(this.form.basic_salary*1+this.form.work_overtime*40-this.form.social_security*1+this.perfect_attendance*1)
  } else {
    this.rules.leave_time[0].required = true
    this.result=Math.trunc(this.form.basic_salary*1+this.form.work_overtime*40-this.form.social_security*1-this.form.basic_salary/this.form.attendance_day*1/8*this.form.leave_time)
  }

  if (await this.check_input()) {
    this.$alert(this.result, '最终薪资', {
      confirmButtonText: '确定',
      callback: action => {
        this.$message({
          type: 'info',
          message: `action: ${ action }`
        });
      }
    });
  }
},
check_input() {
  return new Promise((resolve, reject) => {
    this.$refs.form.validate((valid) => {
      if (valid) {
        resolve(true);
      } else {
        alert('输入格式有误！');
        resolve(false);
      }
    });
  });
}
  }
};
</script>

